<template>
  <div>
    <mrc_ess_table v-if="$route.name=='timedTask'"
                   :headers='headers'
                   pageName='addTimedTask'
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :slotKey="['job_name','status']"
                   :items='desserts'>

      <template #item.job_name='{item}'>
        {{INT(item.job_name)}}
      </template>
      <!-- 状态 -->
      <template v-slot:item.status='{item}'>
        <v-btn rounded
               color="#13a671"
               dark
               x-small
               v-if="item.status==0">{{$t('label_normal')}}</v-btn>
        <v-btn rounded
               color="#999999"
               dark
               x-small
               v-if="item.status==1">{{$t('label_time_out')}}</v-btn>
      </template>

      <!-- 操作 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { getTimedTaskList, deleteTimedTask, executeTimedTask } from '@/service/api/timedTask.js'
export default {
  components: {

  },
  props: {},
  data () {
    return {
      disabled: true,
      list: {},
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
        selectlist: [5, 10, 15, 20],
        pagetext: this.$t('label_Each_page_shows')
      },
      loading: false,
      desserts: [],
    };
  },
  computed: {
    headers () {
      return [
        { text: this.$t('label_The_name_of_the_task'), align: 'start', value: 'job_name', sortable: false },
        { text: this.$t('label_grouping'), value: 'job_group', sortable: false },
        { text: this.$t('label_Call_target_string'), value: 'invoke_target', sortable: false, },
        { text: this.$t('label_Execution_expression'), value: 'cron_expression', sortable: false, },
        { text: this.$t('label_state'), value: 'status', sortable: false, },
        { text: '', value: 'actions', sortable: false, align: 'end', },
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
        { name: this.$t('label_Executed_immediately') },
        { name: this.$t('label_delete') },
      ]
    }
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  created () { },
  watch: {
    $route (v) {
      if (v.path == '/timedTask') {
        this.getlist()
      }
    }
  },
  mounted () {
    // console.log(this.$route);
    // getTimedTaskList()
    this.getlist()
  },

  methods: {
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await getTimedTaskList({
        cpage: this.options.cpage, pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.desserts = res.data
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
      }
      this.loading = false
    },
    addemailtemplate () {
      this.$store.commit('set_subPage_name', '')
      this.$store.commit('set_uuid', '')
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$router.push({
        name: 'addemailtemplate'
      })
    },
    godetail (item) {
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      this.$store.commit('set_subPage_name', item.job_name)
      this.$store.commit('set_uuid', item.uuid)  //
      this.$router.push({
        name: 'detailTimedTask'
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.godetail(item)
          break
        case 1:
          this.$store.commit('set_subPage_name', item.job_name)
          this.$store.commit('set_iconstatus', false)  //行内进图标显示 
          this.$store.commit('set_uuid', item.uuid)  //
          this.$router.push({
            name: 'detailTimedTask'
          })
          break
        case 2:
          this.$myConfirm({ content: this.$t('message_Confirm_that_you_want_to_perform_the_timed_task_immediately') }).then(res => {
            executeTimedTask({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
              }
              else {
                this.$store.commit('newsTips', res.msg)
              }
            })
          }).catch(err => { })
          break
        case 3:
          this.$myConfirm({ content: this.$t('message_Confirm_the_deletion') }).then(res => {
            deleteTimedTask({ uuid: item.uuid }).then(res => {
              if (res.code === '200') {
                this.$store.commit('newsTips', this.$t('message_The_deletion_was_successful'))
                if (this.desserts.length = 1) this.options.cpage--
                this.getlist()
              }
            })
          })
          break
      }
    }
  },
};
</script>

<style scoped>
.cardtitle {
  position: relative;
}
.emailbtn {
  position: absolute;
  right: 70px;
  top: -50px;
}
</style>
